body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.message {
  color: black;
  background: green;
  font-size: 20px;
  text-align: center;
  border-style: solid;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 10px;
}

.error {
  color: black;
  background: red;
  font-size: 20px;
  text-align: center;
  border-style: solid;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 10px;
}



